export default {
  data: function data() {
    return {
      modalData: {
        title: "添加判断题",
        modalBoolean: false
      },
      nowType: "",
      isDisabled: false,
      FormData: {
        data: {
          Question: "",
          Item1: "正确",
          Item2: "错误",
          Answer: "",
          KN: "",
          Chapter: "",
          difficulty: ""
        },
        rules: {
          Question: [{
            required: true,
            message: "不能为空",
            trigger: "blur"
          }],
          Item1: [{
            required: true,
            message: "不能为空",
            trigger: "blur"
          }],
          Item2: [{
            required: true,
            message: "不能为空",
            trigger: "blur"
          }],
          Answer: [{
            required: true,
            message: "不能为空",
            trigger: "change"
          }],
          KN: [{
            required: true,
            message: "不能为空",
            trigger: "blur"
          }],
          Chapter: [{
            required: true,
            message: "不能为空",
            trigger: "change"
          }],
          difficulty: [{
            required: true,
            message: "不能为空",
            trigger: "change"
          }]
        }
      }
    };
  },
  methods: {
    init: function init(type, data) {
      this.nowType = type;

      if (type === "edit") {
        this.isDisabled = false;
        this.modalData.title = "更新判断题";
        console.log("eid:", data);
        this.FormData.data = data;
      } else if (type === "add") {
        this.isDisabled = false;
        this.$refs.formList.resetFields();
        this.modalData.title = "添加判断题";
      } else if (type === "show") {
        this.isDisabled = true;
        this.FormData.data = data;
        this.modalData.title = "查看判断题";
      }

      this.modalData.modalBoolean = true;
    },
    closeModal: function closeModal() {
      this.modalData.modalBoolean = false;
      this.isDisabled = false;
    },
    addNewQS: function addNewQS() {
      var _this = this;

      this.$refs.formList.validate(function (valid) {
        if (valid) {
          _this.$emit("callBack", _this.FormData.data, _this.nowType);

          _this.modalData.modalBoolean = false;
          _this.isDisabled = false;
        } else {
          _this.$Message.destroy();

          _this.$Message.error("请按要求填写");

          return;
        }
      });
    }
  }
};